
.dialog {
    display: grid;
    grid-template-areas: " friend friendPhoto ";
    grid-template-columns: 3fr 1fr;
}

.dialog .friend  a{
    color: white;
    text-decoration: none;
}

.dialog .friend .active {
    color: gold;
}

.dialog .friendPhoto img {
    alignment: right;
    width: 50px;
    height: 50px;
    border-radius: 30px;
}

