.user {
    padding: 10px;
    display: grid;
    grid-template-areas: " visual content ";
    grid-template-columns:2fr 10fr;
}
.visual {
    padding-right: 10px;

}
.visual button {
    width: 90px;
}

.content {
    padding: 10px;
}
.content .name {
    font-weight: bold;
    color: cadetblue;
}

.userPhoto {
    padding: 10px;
    width: 70px;
    height: 70px;
    border-radius: 35px;
}

.selectedPage {
    color: gold;
}
