.dialogs {
    display: grid;
    grid-template-areas: " messages friends ";
    grid-template-columns: 8fr 2fr;
}

.dialogs .dialogsItems {
    grid-area: friends;
    padding: 10px;
    border-left-style: solid;
    border-width: thin;
    background-color: darkturquoise;

}

.messages {
    padding: 10px 10px 0px 10px;

}

.newMessage {
    padding: 5px;
    margin: 0px 10px 0px 10px;
}
.newMessage textarea {
    width: 99%;
}
