
.app-wrapper {

    display: grid;
    grid-template-areas: "edgeleft header header edgeright"
                         "edgeleft nav content edgeright";

    grid-template-rows:  60px 1fr ;
    grid-template-columns:2fr 2fr 10fr  2fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    grid-area: content;
    background-color: azure;
    padding: 10px;
}

.edgeleft {
    grid-area: edgeleft;
}
.edgeright {
    grid-area: edgeright;
}
